<template>
    <div>
        <top-nav title="顾问列表"></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <p>1).这里是分销代理(顾问)列表</p>
        </div>
        <div class="wyu-main" v-loading="loading">
            <div class="wyu-block">
                <div class="wyu-title">
                    <h2>顾问管理</h2>
                </div>
                <el-form  :inline="true" size="mini" :model="search" class="search-form">
                    <el-form-item label="关键字">
                        <el-input size="mini" v-model="search.keyword" placeholder="手机或者昵称"></el-input>
                    </el-form-item>
                    <el-form-item label="顾问组">
                        <el-select size="mini" v-model="search.agentGroup" placeholder="选择顾问组">
                            <el-option label="全部顾问" :value="-999"></el-option>
                            <el-option label="普通顾问" :value="1"></el-option>
                            <el-option label="资深顾问" :value="2"></el-option>
                            <el-option label="专家顾问" :value="3"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="顾问状态">
                        <el-select size="mini" v-model="search.status" placeholder="选择状态">
                            <el-option label="全部状态" :value="-999"></el-option>
                            <el-option label="锁定" :value="0"></el-option>
                            <el-option label="正常" :value="1"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="排序">
                        <el-select size="mini" v-model="search.order" placeholder="选择排序">
                            <el-option label="默认排序" :value="-999"></el-option>
                            <el-option label="子顾问数" :value="1"></el-option>
                            <el-option label="推广订单数" :value="2"></el-option>
                            <el-option label="推广VIP数" :value="3"></el-option>
                            <el-option label="推广注册数" :value="4"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item>
                        <el-button size="mini" @click="seachDatas" type="primary">搜索</el-button>&nbsp;
                        <el-button type="text" @click="search.name='',search.agentGroup=-999,search.bgTime='',search.endTime='',
                        search.status=-999,search.order=-999,seachDatas()">取消搜索</el-button>&nbsp;
                    </el-form-item>
                </el-form>

                <el-table :data="userList" style="width: 100%">

                    <el-table-column prop="user_id" width="80" label="用户ID"></el-table-column>

                    <el-table-column label="头像" width="100">
                        <template slot-scope="scope">
                            <img width="60" :src="scope.row.face"/>
                        </template>
                    </el-table-column>

                    <el-table-column prop="nickname" label="用户昵称"></el-table-column>

                    <el-table-column prop="mobile" label="手机号"></el-table-column>

                    <el-table-column prop="agent_group_mean" label="推广员组"></el-table-column>

                    <el-table-column label="推广贡献" width="300">
                        <template slot-scope="scope">
                            <p>
                                <span style="width: 50%;display: inline-block;">注册：<span style="color:red; padding:0 5px;">{{scope.row.agent_register}}</span> 人</span>
                                <span style="width: 50%;display: inline-block;">订单：<span style="color:red; padding:0 5px;">{{scope.row.agent_order}}</span> 单</span>
                            </p>
                            <p>
                                <span style="width: 50%;display: inline-block;">开通VIP：<span style="color:red; padding:0 5px;">{{scope.row.agent_vip}}</span> 人</span>
                                <span style="width: 50%;display: inline-block;">子顾问：<span style="color:red; padding:0 5px;">{{scope.row.agent_num}}</span> 人</span>
                            </p>
                        </template>
                    </el-table-column>

                    <el-table-column label="注册/最后登录" width="320">
                        <template slot-scope="scope">
                            <p>{{scope.row.reg_time}}</p><p>{{scope.row.last_time}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column label="状态">
                        <template slot-scope="scope">
                            <p v-if="scope.row.is_lock ==1" style="color:red;">冻结中</p>
                            <p v-if="scope.row.is_lock ==0">正常</p>
                        </template>
                    </el-table-column>

                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <div class="list-table-action">
                                <router-link target="_blank" :to="'/agent/manage/view/'+scope.row.user_id"><el-button type="text">业绩查看</el-button></router-link>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
                <br/>
                <div class="page">
                    <div class="page-tool"></div>
                    <div class="page-mian">
                        <el-pagination @current-change="getAgentList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";

    export default {
        name: "agentList",
        components: {TopNav},
        data: function () {
            return {
                loading: false,
                search: {
                    keyword: '',
                    status: -999,
                    bgTime: '',
                    endTime: '',
                    agentGroup: -999,
                    order: -999,
                },
                page: 1,
                limit: 10,
                count: 0,
                datas: [],
                userList: [],
            };
        },
        created() {
            this.getAgentList();
        },
        methods: {
            setPage(res) {

            },
            seachDatas() {
                this.page = 0;
                this.getAgentList();
            },
            getAgentList() {
                var that = this;
                that.loading = true;
                api.post("/shop/admin.agent.agent/getAgentList", {
                    appToken: that.$store.getters.appToken,
                    keyword: that.search.keyword,
                    status: that.search.status,
                    bgTime: that.search.bgTime,
                    endTime: that.search.endTime,
                    agentGroup: that.search.agentGroup,
                    order: that.search.order,
                }).then(res => {
                    that.userList = res.list;
                    that.count = res.count;
                    that.limit = res.limit;
                    that.loading = false;
                }).catch((error) => {
                    that.loading = false;
                });
            },

        }
    };
</script>

